import React, {useState} from 'react';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../style.css'
import workItem6 from "../assets/img/works/6.jpg"
import workItem5 from "../assets/img/works/5.jpg"
import workItem4 from "../assets/img/works/4.jpg"
import workItem3 from "../assets/img/works/3.jpg"
import workItem2 from "../assets/img/works/2.jpg"
import workItem1 from "../assets/img/works/1.jpg"
import Fade from 'react-reveal/Fade';


const Work = () => {
    let [state, editState] = useState([{img: workItem1, title: "This is project Title"},
        {img: workItem2, title: "This is project Title"},
        {img: workItem3, title: "This is project Title"},
        {img: workItem4, title: "This is project Title"},
        {img: workItem5, title: "This is project Title"},
        {img: workItem6, title: "This is project Title"}])
    let workItems = state.map(item => <WorkItem img={item.img} projectTitle={item.title}/>)
    return (
        <section id="work" className="works section-big">
            <Fade left>
                <div className="container">
                    <div className="row">
                        <div className="col-md-12 text-center">
                            <div className="section-title">
                                <h2><span>My</span> Works</h2>
                                <p>The new common language will be more simple and regular than.</p>
                            </div>
                        </div>
                    </div>
                    <div className="row work-items">
                        {workItems}
                    </div>
                </div>
            </Fade>
        </section>
    )
}
const WorkItem = ({img, projectTitle}) => {
    return (
        <div className="col-md-4 col-sm-6 ">
            <div className="item">
                <a href="" className="work-popup">
                    <img src={img} alt=""/>
                    <div className="overlay">
                    </div>
                    <div className="title">{projectTitle}</div>
                </a>
            </div>
        </div>)
}
export default Work;
