import React from 'react';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../style.css'
import Fade from 'react-reveal/Fade';


const Contacts = () => {
    return <section id="contact" className="contact-area section-big">
        <Fade right>
            <div className="container">
                <div className="row">
                    <div className="col-md-12 text-center">
                        <div className="section-title">
                            <h2><span>Contact</span> Me</h2>
                            <p>The new common language will be more simple and regular than.</p>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12">
                        {/*Contact form starts */}
                        <div className="contact-form">
                            <form id="ajax-contact" action="assets/mailer.php" method="post">
                                <div className="form-group in_name">
                                    <input type="text" name="name" className="form-control" id="name"
                                           placeholder="Name"
                                           required="required"/>
                                </div>
                                <div className="form-group in_email">
                                    <input type="email" name="email" className="form-control" id="email"
                                           placeholder="Email" required="required"/>
                                </div>
                                <div className="form-group in_message">
                                    <textarea rows="5" name="message" className="form-control" id="message"
                                              placeholder="Message" required="required"></textarea>
                                </div>
                                <div className="actions">
                                    <input type="submit" value="Send Message" name="submit"
                                           id="submitButton"
                                           className="btn" title="Submit Your Message!"/>
                                </div>
                            </form>
                            {/*Submition status */}
                            <div id="form-messages"></div>
                        </div>
                        {/*Contact form ends*/}
                    </div>
                </div>
            </div>
        </Fade>
    </section>
}


export default Contacts;
